import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Home from '../../Pages/Home/Home';
import AppBar from '../../Pages/AppBar/AppBar';
import data from '../../Data/data.json';
import LeftPanel from './LeftPanel/LeftPanel';
import ScrollPanel from './ScrollPanel/ScrollPanel';
import FooterPanel from './FooterPanel/FooterPanel';
import FeedbackIcon from '@material-ui/icons/Feedback';

const useStyles = makeStyles((theme) => ({
  container: {
    height: 'auto',
    backgroundImage: 'linear-gradient( 150deg, #e7b4de 30%, #86a1e2 70%, #a0cffa 100%)',
    borderRadius: '5px',
    boxShadow: '-3px -2px gray',
    margin: '1%',
    flexGrow: 1,
    minWidth: '1215px',
    minWidth: '1215px',
    minHeight: '720px',
  },
  header:{
    padding: theme.spacing(3),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  pages: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    minHeight: '550px',
    backgroundColor: '#e8ffc8',
    border : "10px solid #fff"
  },
  logo: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    borderRadius:'50px',
  },
  paper: {
    padding: theme.spacing(4),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },

  closeBtn :{
    
    fontSize: '3em',
    color: 'rgb(3, 3, 85)',
    marginTop: '5px',
    position: 'fixed',
    marginTop: '240px',
    marginLeft: '1165px',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    cursor: 'pointer',
  }
}))
function DuiLayout({pages,currentPage,pageIcons}) {
  const classes = useStyles();
  const theme = useTheme();
  return (<React.Fragment> 
    <CssBaseline />
    <Container >
      <Typography component="div" className={classes.container}>
      <FeedbackIcon className={classes.closeBtn} titleAccess={'feedback'}/>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="stretch"
          spacing={1}>
          <Grid item xs={12} sm={11}>
          <AppBar tabs={pages} selection={currentPage}></AppBar>
          </Grid>
          <Grid item xs={12} sm={11}>
            <ScrollPanel/>
          </Grid>
          <Grid item xs={12} sm={2}>
            <LeftPanel currenPage={currentPage[0]} pageIcons={pageIcons}/>
          </Grid>
          <Grid item xs={12} sm={9}>
            <Paper className={classes.pages}>{pages[currentPage[0]]}</Paper>
          </Grid>
          <Grid item xs={12} sm={11}>
            <FooterPanel/>
        </Grid>
        </Grid>
        
      </Typography>
      
    </Container>
    
  </React.Fragment>);
}

export default DuiLayout;